import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import '../css/EventModal.css';
import {
  Accordion, Button, Form, Icon, Image, Modal, TextArea,
} from 'semantic-ui-react';
import { addDays } from 'date-fns';
import 'react-date-range/dist/styles.css'; // main css file
import 'react-date-range/dist/theme/default.css'; // theme css file
import { DateRangePicker } from 'react-date-range';
import { endpoint } from '../lib/vars';

function EventModal({ open, setOpen }) {
  const [date, setDate] = useState([
    {
      startDate: new Date(),
      endDate: addDays(new Date(), 7),
      key: 'selection',
    },
  ]);

  const [savedDates, setSavedDates] = useState([]);

  const [activeIndex, setActiveIndex] = useState(-1);
  const [dateToRemove, setDateToRemove] = useState(null);

  const [title, setTitle] = useState(null);
  // eslint-disable-next-line no-unused-vars
  const [description, setDescription] = useState(null);
  const [image, setImage] = useState(null);

  const handleClick = (e, titleProps) => {
    const { index } = titleProps;
    const newIndex = activeIndex === index ? -1 : index;

    setActiveIndex(newIndex);
  };

  const addDate = () => {
    const newDates = [...savedDates, date[0]];
    setSavedDates(newDates);
  };

  const createEvent = () => {
    const options = {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        title, description, image, dates: savedDates,
      }),
    };

    fetch(`${endpoint}events`, options)
      .then(() => setOpen(false));
  };

  useEffect(() => {
    if (dateToRemove === null) return;
    const newDates = [...savedDates];
    newDates.splice(dateToRemove, 1);
    setSavedDates(newDates);
    setDateToRemove(null);
  }, [dateToRemove]);

  return (
    <Modal
      onClose={() => setOpen(false)}
      onOpen={() => setOpen(true)}
      open={open}
      size="fullscreen"
    >
      <Modal.Header>Create an event</Modal.Header>
      <Modal.Content image>
        {image && <Image size="medium" src={image} wrapped />}
        <Modal.Description style={{ width: '100%' }}>
          <Accordion styled fluid style={{ width: '100%' }}>
            <Accordion.Title
              active={activeIndex === 0}
              index={0}
              onClick={handleClick}
            >
              <Icon name="dropdown" />
              Add dates
            </Accordion.Title>
            <Accordion.Content active={activeIndex === 0} style={{ width: '100%' }}>
              <DateRangePicker
                onChange={(item) => setDate([item.selection])}
                showSelectionPreview
                moveRangeOnFirstSelection={false}
                months={2}
                ranges={date}
                direction="horizontal"
                className="date-picker"
              />
              <Button onClick={addDate}>Add</Button>
              <div className="date-list">
                {savedDates.map((d, idx) => (
                  <div className="date-item" key={d.startDate.toString() + d.endDate.toString() + Math.random()}>
                    <Icon name="close" color="red" size="large" verticalAlign="middle" onClick={() => setDateToRemove(idx)} className="remove-icon" />
                    {`From ${d.startDate.toLocaleDateString()} - To ${d.endDate.toLocaleDateString()}`}
                  </div>
                ))}
              </div>
            </Accordion.Content>
          </Accordion>
          <Form style={{ marginTop: '1em' }}>
            <Form.Input fluid label="Title" placeholder="Title" value={title ?? ''} onChange={(e, { value }) => setTitle(value)} />
            <TextArea label="Description" rows="5" placeholder="Description" value={description ?? ''} onChange={(e, { value }) => setDescription(value)} />
            <Form.Input fluid label="Image" placeholder="Description" value={image ?? ''} onChange={(e, { value }) => setImage(value)} />
          </Form>
        </Modal.Description>
      </Modal.Content>
      <Modal.Actions>
        <Button color="black" onClick={() => setOpen(false)}>
          Nope
        </Button>
        <Button
          content="Yep, that's me"
          labelPosition="right"
          icon="checkmark"
          onClick={createEvent}
          positive
        />
      </Modal.Actions>
    </Modal>
  );
}

EventModal.propTypes = {
  open: PropTypes.bool.isRequired,
  setOpen: PropTypes.func.isRequired,
};

export default EventModal;
