import React from 'react';
import PropTypes from 'prop-types';
import { Box } from '@mui/material';
import '../css/CentralContainer.css';
import '../css/Tiles.css';
import Events from './Events';
import Tile from './Tile';
import event from '../images/event.jpg';

function CentralContainer({ menu, setMenu }) {
  return (
    <Box id="central-container">
      {menu === 'home'
        && (
        <div id="tiles-menu">
          <Tile title="Events" color="yellow" onClick={() => setMenu('events')} image={event} />
          <Tile title="Dummy" color="blue" image={event} />
          <Tile title="Dummy" color="green" image={event} />
        </div>
        )}
      {menu === 'events' && <Events />}
    </Box>
  );
}

CentralContainer.propTypes = {
  menu: PropTypes.string.isRequired,
  setMenu: PropTypes.func.isRequired,
};

export default CentralContainer;
