import React from 'react';
import PropTypes from 'prop-types';
import {
  Box, Drawer, List, ListItem, ListItemButton, ListItemIcon, ListItemText,
} from '@mui/material';
import { Home, Event } from '@mui/icons-material';
import '../css/SideMenu.css';
import factorioWheel from '../images/factorio-wheel.png';

function SideMenu({ setMenuOpen, setMenu, menuOpen }) {
  return (
    <div>
      <button style={{ backgroundImage: `url(${factorioWheel})` }} type="button" aria-label="menu" onClick={() => setMenuOpen(true)} id="menu-btn" />
      <React.Fragment key="menu">
        <Drawer anchor="left" open={menuOpen} onClose={() => setMenuOpen(false)}>
          <Box sx={{ width: 250 }}>
            <List>
              <ListItem key="home" disablePadding onClick={() => setMenu('home')}>
                <ListItemButton>
                  <ListItemIcon>
                    <Home />
                  </ListItemIcon>
                  <ListItemText primary="Home" />
                </ListItemButton>
              </ListItem>
              <ListItem key="events" disablePadding onClick={() => setMenu('events')}>
                <ListItemButton>
                  <ListItemIcon>
                    <Event />
                  </ListItemIcon>
                  <ListItemText primary="Events" />
                </ListItemButton>
              </ListItem>
            </List>
          </Box>
        </Drawer>
      </React.Fragment>
    </div>
  );
}

SideMenu.propTypes = {
  setMenuOpen: PropTypes.func.isRequired,
  setMenu: PropTypes.func.isRequired,
  menuOpen: PropTypes.bool.isRequired,
};

export default SideMenu;
