import React, { useState } from 'react';
import PropTypes from 'prop-types';
import {
  Card, CardActions, CardContent, CardMedia, Typography,
} from '@mui/material';
import {
  Accordion, Button, Header, Icon, Image, Input, Label, Modal,
} from 'semantic-ui-react';
import '../css/EventCard.css';

function EventCard({
  id, title, description, image, dates,
}) {
  const [open, setOpen] = useState(false);
  const [activeIndex, setActiveIndex] = useState(-1);
  const [choices, setChoices] = useState([]);

  const handleClick = (e, titleProps) => {
    const { index } = titleProps;
    const newIndex = activeIndex === index ? -1 : index;

    setActiveIndex(newIndex);
  };

  const addChoice = (idx, val) => {
    let currChoices = [...choices];
    const exist = currChoices.find((c) => c.idx === idx);
    if (exist) {
      if (exist.val === val) {
        currChoices = currChoices.filter((c) => c.idx !== idx);
      } else exist.val = val;
    } else {
      currChoices.push({ idx, val });
    }
    setChoices([...currChoices]);
  };

  return (

    <Card className="card" key={id}>
      <CardMedia
        component="img"
        height="140"
        image={image}
        alt={title}
      />
      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          {title}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          {description.length > 100 ? `${description.substring(0, 100)}...` : description}
        </Typography>
        {dates.find((d) => d.validated === true) && (
        <Label>
          <Icon name="calendar alternate" />
        </Label>
        )}
      </CardContent>
      <CardActions sx={{ flexDirection: 'row-reverse' }}>
        <Button size="small" onClick={() => setOpen(true)}>Details</Button>
      </CardActions>
      <Modal
        size="large"
        open={open}
        onOpen={() => setOpen(true)}
        onClose={() => setOpen(false)}
      >
        <Modal.Header>{title}</Modal.Header>
        <Modal.Content image>
          {image && <Image size="medium" src={image} wrapped />}
          <Modal.Description fluid>
            <Accordion styled fluid>
              <Accordion.Title
                active={activeIndex === 0}
                index={0}
                onClick={handleClick}
                style={{ width: '100%' }}
              >
                <Icon name="dropdown" />
                Available dates
              </Accordion.Title>
              <Accordion.Content active={activeIndex === 0}>
                <div className="date-flex-containter">
                  {dates.map((d, idx) => (
                    <Input className="date-input" key={d.startDate.toString() + d.endDate.toString() + Math.random()} labelPosition="right" type="text" placeholder="Amount">
                      <Label className="cursor-label" color={choices.find((c) => c.idx === idx && c.val === true) ? 'green' : ''} basic onClick={() => addChoice(idx, true)}><Icon className="no-margin-icon" name="check" color="green" /></Label>
                      <input value={`From ${d.startDate.toLocaleDateString()} - To ${d.endDate.toLocaleDateString()}`} />
                      <Label className="cursor-label" color={choices.find((c) => c.idx === idx && c.val === false) ? 'red' : ''} basic onClick={() => addChoice(idx, false)}><Icon className="no-margin-icon" name="close" color="red" /></Label>
                    </Input>
                  ))}
                </div>
              </Accordion.Content>
            </Accordion>
            <Header as="h4">Description</Header>
            <p>{description}</p>
          </Modal.Description>
        </Modal.Content>
      </Modal>
    </Card>

  );
}

EventCard.propTypes = {
  id: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  dates: PropTypes.arrayOf(PropTypes.shape(PropTypes.string)).isRequired,
};

export default EventCard;
