import React from 'react';
import PropTypes from 'prop-types';
import '../css/Tile.css';

function Tile({
  title, onClick, color, image,
}) {
  return (
    <div className="tiles-menu-item" style={{ backgroundColor: color, backgroundImage: `url(${image})` }} onKeyDown={onClick} onClick={onClick} role="button" tabIndex={0}>
      <div className="tiles-menu-card-title">
        <p>{title}</p>
      </div>
      {/* <img src={image} alt="event" /> */}
    </div>
  );
}

Tile.propTypes = {
  title: PropTypes.string.isRequired,
  color: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
};

export default Tile;
