import React, { useEffect, useState } from 'react';
import { Box } from '@mui/material';
import { Button, Dimmer, Loader } from 'semantic-ui-react';
import '../css/Events.css';
import EventModal from './EventModal';
import { endpoint } from '../lib/vars';
import EventCard from './EventCard';

function Events() {
  const [createModal, setCreateModal] = useState(false);
  const [loader, setLoader] = useState(false);
  const [data, setData] = useState([]);

  const loadData = () => {
    setLoader(true);
    const options = {
      method: 'GET',
      headers: { 'Content-Type': 'application/json' },
    };

    fetch(`${endpoint}events`, options)
      .then(async (response) => {
        const resp = await response.json();
        const body = JSON.parse(resp.body);
        body.forEach((ev, i) => {
          ev.dates.forEach((d, j) => {
            body[i].dates[j].startDate = new Date(d.startDate);
            body[i].dates[j].endDate = new Date(d.endDate);
          });
        });
        console.log(body);
        setData(body);
        setLoader(false);
      });
  };

  useEffect(() => {
    loadData();
  }, []);

  return (
    <Box id="events">
      <Dimmer active={loader}>
        <Loader />
      </Dimmer>
      <Box className="cards">
        <Box className="card-container">
          {data.map((item) => (
            <EventCard
              key={item.id}
              id={item.id}
              title={item.title}
              description={item.description}
              image={item.image}
              dates={item.dates}
            />
          ))}
        </Box>
      </Box>
      <Box className="actions">
        <Button onClick={() => setCreateModal(true)}>
          New
        </Button>
      </Box>
      <EventModal open={createModal} setOpen={setCreateModal} />
    </Box>
  );
}

export default Events;
